<template>
  <v-dialog
    v-model="localDialog"
    @afterLeave="reset()"
    :persistent="editMode"
    width="auto"
  >
    <v-progress-circular
      v-if="isLoadingDetail"
      color="secondary"
      size="65"
      indeterminate
    ></v-progress-circular>
    <v-card v-else width="40rem">
      <v-card-title
        v-if="!itsMe"
        class="bg-info d-flex align-center justify-space-between"
        :class="{ 'bg-warning': editMode }"
        ><div>
          <div v-if="editMode">
            <v-icon size="small" class="mr-2">mdi-pencil</v-icon>
            <span>
              Editar Conta
              <span class="font-weight-light">{{ agent.username }}</span>
            </span>
          </div>
          <div v-else>
            <v-icon size="small" class="mr-2">mdi-information-outline</v-icon>
            <span>
              Detalhes da Conta
              <span class="font-weight-light">{{ agent.username }}</span>
            </span>
          </div>
          <!-- <span class="text-h6">{{ title }}</span> -->
        </div>
        <div>
          <v-chip
            v-if="agent.organization"
            class="mr-2"
            variant="tonal"
            color="warning"
          >
            {{ agent.organization.organization_name }}</v-chip
          >
          <v-chip label variant="flat" :color="roleColor(agent.type)">{{
            agent.type
          }}</v-chip>
        </div></v-card-title
      >
      <v-card-text>
        <v-form v-model="agentForm.valid">
          <v-row class="mt-2">
            <v-col cols="12">
              <v-text-field
                v-model.trim="agentForm.agent.name"
                label="Nome"
                placeholder="Insira o nome"
                prepend-inner-icon="mdi-account"
                color="secondary"
                density="compact"
                variant="outlined"
                :readonly="!editMode"
                :rules="[agentForm.rules.required, agentForm.rules.maxLength1]"
              ></v-text-field>
            </v-col>
            <!-- <v-col cols="5">
              <v-text-field
                v-model="agentForm.agent.organization"
                :rules="agentForm.rules.organization"
                label="Organização"
                prepend-icon="mdi-domain"
                color="secondary"
                density="compact"
                variant="outlined"
                readonly
              ></v-text-field>
            </v-col> -->
            <v-col cols="7">
              <v-text-field
                v-model.trim="agentForm.agent.address"
                label="Morada"
                placeholder="Insira a morada"
                prepend-inner-icon="mdi-map-marker"
                color="secondary"
                density="compact"
                variant="outlined"
                :readonly="!editMode"
                :rules="[agentForm.rules.required, agentForm.rules.maxLength2]"
              ></v-text-field>
            </v-col>
            <v-col cols="5">
              <v-text-field
                v-model.trim="agentForm.agent.city"
                label="Cidade"
                placeholder="Insira a cidade"
                prepend-inner-icon="mdi-city"
                color="secondary"
                density="compact"
                variant="outlined"
                :readonly="!editMode"
                :rules="[agentForm.rules.required, agentForm.rules.maxLength1]"
              ></v-text-field>
            </v-col>
            <v-col cols="7" v-if="!editMode || itsMe">
              <v-text-field
                v-model.trim="agentForm.agent.email"
                label="Email"
                prepend-inner-icon="mdi-email"
                color="secondary"
                density="compact"
                variant="outlined"
                readonly
                :disabled="itsMe"
                :rules="[agentForm.rules.required, agentForm.rules.email]"
              ></v-text-field>
            </v-col>
            <v-col cols="5" v-if="!editMode || itsMe">
              <v-text-field
                v-model.trim="agentForm.agent.phone"
                label="Telefone"
                prepend-inner-icon="mdi-phone"
                prefix="+351"
                counter="9"
                color="secondary"
                density="compact"
                variant="outlined"
                readonly
                :disabled="itsMe"
                :rules="[agentForm.rules.required, agentForm.rules.phone]"
              ></v-text-field>
            </v-col>
            <v-col
              v-if="!itsMe"
              cols="12"
              class="d-flex align-center justify-space-around"
            >
              <v-switch
                v-model="agentForm.agent.accepted"
                color="secondary"
                label="Aceite"
                :readonly="!editMode"
              ></v-switch>
              <v-switch
                v-model="agentForm.agent.blocked"
                color="secondary"
                label="Bloqueado"
                :readonly="!editMode"
              ></v-switch>
              <v-switch
                v-model="agentForm.agent.active"
                color="secondary"
                label="Ativo"
                :readonly="!editMode"
              ></v-switch>
            </v-col>
          </v-row>
        </v-form>
      </v-card-text>
      <v-divider></v-divider>
      <v-card-actions v-if="editMode" class="d-flex justify-space-around">
        <v-btn
          color="error"
          variant="tonal"
          icon="mdi-close"
          @click="localDialog = false"
        ></v-btn>
        <v-btn
          color="secondary"
          variant="tonal"
          icon="mdi-check"
          :loading="isLoading"
          :disabled="!agentForm.valid"
          @click="editAgent()"
        ></v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import { useAccountStore } from "@/stores/accountStore";
import { useGeneralStore } from "@/stores/generalStore";

import axios from "axios";
export default {
  name: "AgentDetail",
  props: {
    agent: {
      type: Object,
    },
    me: {
      type: Number,
      required: false,
      default: null,
    },
    editMode: {
      type: Boolean,
      required: false,
      default: false,
    },
    dialog: {
      type: Boolean,
      required: true,
    },
  },
  setup() {
    const accountStore = useAccountStore();
    const generalStore = useGeneralStore();
    return { accountStore, generalStore };
  },
  data() {
    return {
      isLoadingDetail: false,

      isLoading: false,
      agentForm: {
        agent: {
          id: null,
          role: null,
          username: null,
          organization: null,
          email: null,
          phone: null,
          city: null,
          address: null,
          accepted: false,
          blocked: false,
          active: false,
        },
        rules: {
          required: (v) => !!v || "Campo obrigatório",
          maxLength1: (v) => (v && v.length <= 25) || "Máximo 25 caracteres",
          maxLength2: (v) => (v && v.length <= 50) || "Máximo 50 caracteres",
          phone: (v) => (v && v.length === 9) || "Número inválido",
          email: (v) => /.+@.+\..+/.test(v) || "Email inválido",
          // organization: [
          //   (v) => !!v || "Organização é obrigatória",
          //   (v) => (v && v.length <= 50) || "Máximo 50 caracteres",
          // ],
          // email: [
          //   (v) => !!v || "Email é obrigatório",
          //   (v) => /.+@.+\..+/.test(v) || "Email inválido",
          // ],
          // phone: [
          //   (v) => !!v || "Telefone é obrigatório",
          //   (v) => (v && v.length <= 9) || "Máximo 9 caracteres",
          // ],
          // city: [
          //   (v) => !!v || "Cidade é obrigatória",
          //   (v) => (v && v.length <= 25) || "Máximo 25 caracteres",
          // ],
          // address: [
          //   (v) => !!v || "Endereço é obrigatório",
          //   (v) => (v && v.length <= 50) || "Máximo 50 caracteres",
          // ],
        },
        valid: false,
      },
    };
  },
  watch: {
    dialog: {
      handler() {
        if (this.dialog) {
          this.onOpenDialog();
        }
      },
    },
  },
  computed: {
    title() {
      return this.editMode ? "Editar Conta " : "Detalhes da Conta ";
    },
    itsMe() {
      return this.me !== null;
    },
    localDialog: {
      get() {
        return this.dialog;
      },
      set(value) {
        this.$emit("update:dialog", value);
      },
    },
    roleColor() {
      return (role) => {
        switch (role) {
          case "Administrador":
            return "error";
          case "Organização":
            return "warning";
          case "Produtor":
            return "success";
          case "Agente de Campo":
            return "info";
          default:
            return "primary";
        }
      };
    },
  },
  methods: {
    async onOpenDialog() {
      // this.agentForm.agent = { ...this.agent };
      if (this.itsMe) {
        await this.getAgentDetail(this.me);
      } else {
        await this.getAgentDetail(this.agent.id);
      }
    },

    async getAgentDetail(agent_id) {
      this.isLoadingDetail = true;
      // await new Promise((resolve) => setTimeout(resolve, 2000));
      await axios
        .request({
          url: "api/agents/" + agent_id,
          method: "GET",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          this.agentForm.agent = response.data;
          console.log(response.data);
        })
        .catch((error) => {
          console.log(error);
        })
        .finally(() => {
          this.isLoadingDetail = false;
        });
    },
    editAgent() {
      if (this.itsMe) {
        this.editMe();
      } else {
        this.editOther();
      }
    },

    async editMe() {
      this.isLoading = true;
      let payload = {
        entity_name: this.agentForm.agent.name,
        entity_email: this.agentForm.agent.email,
        entity_phone: this.agentForm.agent.phone,
        entity_city: this.agentForm.agent.city,
        entity_address: this.agentForm.agent.address,
      };
      await axios
        .request({
          url: "api/agents/me",
          method: "patch",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          data: payload,
        })
        .then((response) => {
          this.isLoading = false;
          this.generalStore.successSnackbar("Conta editada com sucesso");
          this.$emit("refresh");
          this.localDialog = false;
        })
        .catch((error) => {
          console.log(error);
          this.isLoading = false;
          this.generalStore.errorSnackbar("Erro ao editar a conta");
        });
    },
    async editOther() {
      this.isLoading = true;
      let payload = {
        agent_accepted: this.agentForm.agent.accepted,
        agent_blocked: this.agentForm.agent.blocked,
        agent_active: this.agentForm.agent.active,
        entity_name: this.agentForm.agent.name,
        entity_email: this.agentForm.agent.email,
        entity_phone: this.agentForm.agent.phone,
        entity_city: this.agentForm.agent.city,
        entity_address: this.agentForm.agent.address,
      };
      await axios
        .request({
          url: "api/agents/" + this.agent.id,
          method: "patch",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          data: payload,
        })
        .then((response) => {
          this.isLoading = false;
          this.generalStore.successSnackbar("Conta editada com sucesso");
          this.$emit("refresh");
          this.localDialog = false;
        })
        .catch((error) => {
          console.log(error);
          this.isLoading = false;
          this.generalStore.errorSnackbar("Erro ao editar a conta");
        });
    },
    // async getAgentDetail() {
    //   this.isLoadingDetail = true;
    //   await axios
    //     .request({
    //       url: "api/agents/" + this.agent.id,
    //       method: "GET",
    //       headers: {
    //         Authorization: "Bearer " + this.accountStore.account.token,
    //       },
    //     })
    //     .then((response) => {
    //       this.agentForm.agent = response.data;
    //     })
    //     .catch((error) => {
    //       console.log(error);
    //     })
    //     .finally(() => {
    //       this.isLoadingDetail = false;
    //     });
    // },
    openDialogwithID(id) {
      this.agentForm.agent.id = id;
      this.localDialog = true;
    },
    reset() {
      this.agentForm.agent = {
        id: null,
        role: null,
        username: null,
        organization: null,
        email: null,
        phone: null,
        city: null,
        address: null,
        accepted: false,
        blocked: false,
        active: false,
      };
    },
  },
};
</script>

<style></style>
